<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";

const toLower = (text) => {
  return text.toString().toLowerCase();
};
// 搜尋條件
const searchByName = (items, term) => {
  if (term) {
    return items.filter(
      (item) =>
        toLower(item.usergroup_Desc).includes(toLower(term)) ||
        toLower(item.col3).includes(toLower(term)) ||
        toLower(item.col5).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "authManagementComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      loading: false,
      options: [],
      search: "",
      form: {
        col1: "",
        col2: "",
        col3: "",
        col4: "",
      },
      rules: {
        col1: [{ required: true, message: "必填", trigger: "change" }],
        col2: [{ required: true, message: "必填", trigger: "change" }],
        col3: [{ required: true, message: "必填", trigger: "change" }],
        col4: [{ required: true, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        { id: "index", label: "項次", width: "70" },
        { id: "usergroup_Desc", label: "角色描述", width: "200" },
        { id: "col3", label: "廠區名稱", width: "200" },
        { id: "fab_Description", label: "FAB場別", width: "200" },
        { id: "col5", label: "管理者工號", width: "200" },
        { id: "emp_Email", label: "email", width: "200" },
        { id: "col7", label: "部門/分機", width: "200" },
      ],
      location_Id: "",
      fabOptions: [],
      data: [],
      tableData: [],
      tableList: [],
      fab: [],
      groups: [],
      location: [],
    };
  },
  computed: {},
  methods: {
    async searchInput(evt) {
      const val = evt.target.value;
      // 輸入框有資料時篩選資料，沒有就把全部的資料塞回去
      if (val) {
        if (evt.code === "Enter" || evt.code === "NumpadEnter") {
          this.tableData = searchByName(this.data, val);
        }
      } else {
        if (evt.code === "Enter" || evt.code === "NumpadEnter") {
          this.tableData = this.data;
        }
      }
    },
    async qryPersonPermission() {
      const res = await this.$store.dispatch("qryPersonPermission");
      const data = res.data.data;
      this.data = JSON.parse(JSON.stringify(data.allPeople));
      this.data.forEach((e, index) => {
        e.index = index + 1;
        e.col3 = `${e.ld2 || ""}${e.ld3 ? `(${e.ld3})` : ""}`;
        e.col5 = `${e.emp_Name}(${e.user_No})`;
        e.col7 = `${e.emp_Dept_Full_Name}/${e.emp_Extension_No}`;
      });
      this.tableData = JSON.parse(JSON.stringify(this.data));
      this.tableList = this.tableData.slice(0, 10);
      this.fab = data.fab;
      this.groups = data.groups;
      this.location = data.location;
    },
    locationChangeHandler(val) {
      this.location_Id = val.location_Id;
      this.fabOptions = this.fab.filter((e) => e.id === val.id);
      this.form.col3 = "";
    },
    // 如果是廠級助理，廠務中控室，公安防災中心，廠區承攬商窗口就秀出FAB廠別，以及控制輸入框欄寬
    displayFabHandler() {
      if (
        this.form.col1 === "FAB_USER" ||
        this.form.col1 === "FAC_USER" ||
        this.form.col1 === "SHE_USER" ||
        this.form.col1 === "GA_USER"
      ) {
        return true;
      } else {
        return false;
      }
    },
    async allPeopleRemoteMethod(evt) {
      this.loading = true;
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.loading = false;
        if (!evt.target.value) return;
        const res = await this.$store.dispatch("qryEmp", evt.target.value);
        if (res.data.isOK) {
          this.options = res.data.data;
        }
      }
    },
    allPeopleChange() {
      this.options = [];
    },
    // 取得欄位
    getUserGroupDesc(userGroup) {
      const userGroup_Desc = this.groups.find(
        (e) => e.userGroup === userGroup
      ).userGroup_Desc;
      return userGroup_Desc;
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    async confirmHandler(rid) {
      const res = await this.$store.dispatch(
        "delPersonPermission",
        encodeURIComponent(rid)
      );
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: "5000",
          showClose: true,
          message: "刪除成功",
        });
        this.qryPersonPermission();
      }
    },
    addForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const obj = {
            UserGroup: this.form.col1,
            UserGroup_Desc: this.getUserGroupDesc(this.form.col1),
            Emp_No: this.form.col4,
            Location_Id: this.location_Id,
            Fab_Id: this.form.col3,
          };
          const res = await this.$store.dispatch("insPersonPermission", obj);
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: "5000",
              showClose: true,
              message: "送出成功",
            });
            this.qryPersonPermission();
          }
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
  },
  mounted() {
    this.$store.state.currentPage = 1;
    this.qryPersonPermission();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./authManagement.html"></template>
